.page-container {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
*{
  box-sizing:border-box
}

body{
  margin:0
}

.Parallax{
  display:-webkit-flex;
  display:flex
}

.Parallax__background{
  width:100%;
  height:1000vh;
  position:fixed;
  background-size:contain;
  background-repeat:repeat-y;
  background-position:center;
  top:0%;
  left:0%;
  z-index:1
}

.Parallax__background-triangles{
  width:100%;
  height:100%;
  background-size:contain;
  background-repeat:no-repeat;
  position:absolute;
  left:50%;
  z-index:2;
  bottom:0
}

.Parallax__background{
  background-image:url(/static/media/Background.854905c4.png)
}

.Parallax__background-triangles{
  background-image:url(/static/media/Foreground.be847913.png)
}

.Parallax__content{
  z-index:10
}

